<style>
.CDEK-widget__popup-mask{
    z-index: 100;
}
</style>
<script>
import ISDEKWidjet from '~/assets/a'
export default{
    el: '#s',
    props:['products'],
    mounted(){
        var insertScript = document.createElement('script')
    insertScript.setAttribute('src', 'https://widget.cdek.ru/widget/scripts/ipjq.js')
    document.body.appendChild(insertScript)
    insertScript = document.createElement('script')
    insertScript.setAttribute('src', 'https://api-maps.yandex.ru/2.1/?apikey=f4e034c2-8c37-4168-8b97-99b6b3b268d7&amp;lang=ru_RU')
    document.body.appendChild(insertScript)
    insertScript = document.createElement('script')
    insertScript.setAttribute('src', 'https://widget.cdek.ru/widget/scripts/jquery.mCustomScrollbar.concat.min.js')
    document.body.appendChild(insertScript)
    },
    data(){
        return{
            
            orderWidjet: ISDEKWidjet({
        popup: true,
        defaultCity: 'Тамбов',
        cityFrom: 'Уварово',
        goods: this.get_goods(), // установим данные о товарах из корзины,
        onReady : function(){ // на загрузку виджета отобразим информацию о доставке до ПВЗ
            ipjq('#linkForWidjet').css('display','inline');
        },
        onChoose:(info) => { // при выборе ПВЗ: запишем номер ПВЗ в текстовое поле и доп. информацию
           // ipjq('[name="chosenPost"]').val(info.id);
            //ipjq('[name="addresPost"]').val(info.PVZ.Address);
            // расчет стоимости доставки
            //var price = Math.ceil( info.price/100 ) * 100;
            //ipjq('[name="pricePost"]').val(price);
            //ipjq('[name="timePost"]').val(info.term);
            this.$emit('onChoise', info)
            this.orderWidjet.close(); // закроем виджет
        }
    })
        }
    },
    methods:{
        get_goods(){
            let list = []
            this.products.forEach(element => {
                
                list.push({ length : element.productItem.dimensions.length,
                     width : element.productItem.dimensions.width,
                      height : element.productItem.dimensions.height, weight : element.productItem.weight.weight/1000 })
            });
            console.log(list)
            return list
        }
    }
}
</script>